import { combineEpics } from 'redux-observable';
import { Observable } from 'rxjs/Rx';
import { ajax } from 'rxjs/observable/dom/ajax';
import * as R from 'ramda';

import { Action, ErrorAction } from 'utilities.js';

// Actions
export const GET_USER_REPOS_AJAX = 'UserProfile/GET_USER_REPOS_AJAX'
export const GET_USER_REPOS_AJAX_STARTED = 'UserProfile/GET_USER_REPOS_AJAX_STARTED'
export const GET_USER_REPOS_AJAX_COMPLETED = 'UserProfile/GET_USER_REPOS_AJAX_COMPLETED';
export const GET_USER_REPOS_AJAX_ERROR = 'UserProfile/GET_USER_REPOS_AJAX_ERROR';

// Action Creators
export const getUserRepositoriesDispatch = () => Action(GET_USER_REPOS_AJAX);

// Define the initial state for the reducer
const initialState = {
    isUserLoading: false,
    isUserReposLoading: false,
    isResumeVisible: false,
    loadingMessage: '',
    data: {}
};

// Reducer
export function reducer(state = initialState, action) {
    const mergeToState = R.merge(state)

    switch (action.type) {
        case GET_USER_REPOS_AJAX_STARTED:
            return mergeToState({
                isUserReposLoading: true,
                loadingMessage: 'Fetching data...'
            });
        case GET_USER_REPOS_AJAX_COMPLETED:
            return mergeToState({
                isUserReposLoading: false,
                loadingMessage: 'Fetched data!'
            });
        case GET_USER_REPOS_AJAX_ERROR:
            return mergeToState({
                isUserReposLoading: false,
                loadingMessage: action.payload
            });
        default:
            return state;
    }
}

const defaultHeaders = {
    'Access-Control-Request-Headers': null
};
  
  export const get = (url, headers) => ajax.get(url, Object.assign({}, defaultHeaders, headers));

// Epics
function getUserReposEpic(action$, store) {
    return action$.ofType(GET_USER_REPOS_AJAX)
        .switchMap(function(action) {
            const limit = 10;
            return get(`http://www.snagfilms.com/apis/films.json?limit=${limit}`)
                .map((response) => Action(GET_USER_REPOS_AJAX_COMPLETED, response))
                .catch((error) => ErrorAction(GET_USER_REPOS_AJAX_ERROR, error))
                .startWith(Action(GET_USER_REPOS_AJAX_STARTED))
        })
};

export const homeNavigationEpic = combineEpics(
    getUserReposEpic
);



