import './HomePage.css';

import { connect } from 'react-redux';
import { lifecycle } from 'recompose'
import React from 'react';

import { getUserRepositoriesDispatch } from 'ducks/HomeNavigation.duck.js';

function HomePage(props) {

    return (
        <div>

        </div>
    );
}

const component = lifecycle({
    componentDidMount() {
      this.props.getUserRepos();
    }
  })(HomePage);

function mapStateToProps(state) {
    return {
        homeNavigation: state.homeNavigation.data
    };
}

function mapDispatchToProps(dispatch) {
    return {
        getUserRepos: () => dispatch(getUserRepositoriesDispatch())
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(component);


